.profile h1{
	font-weight: normal;
	font-size: 20px;
	margin:10px 0 0 0;
}

.profile h2{
	font-size: 14px;
	font-weight: lighter;
	margin-top: 5px;
}

.profile .img-box{
	opacity: 1;
	display: block;
    position: relative;
}

.profile .img-box:after{
	content:"";
	opacity: 0;
	background-color: rgba(0, 0, 0, 0.75);
	position: absolute;
	right: 0;
	left: 0;
	top: 0;
	bottom: 0;
}

.img-box {
	position: relative;
  	width: 50%;
}
.img-box ul{
	position: absolute;
	z-index: 2;
	bottom: 50px;
	text-align: center;
	width: 100%;
	padding-left: 0px;
	height: 0px;
	margin:0px;
	opacity: 0;
}

.img-box ul i{
	font-size: 20px;
	letter-spacing: 10px;
}

.img-box ul li{
	width: 60px;
    height: 60px;
    text-align: center;
    border: 2px solid orange;
    margin: 2px;
    padding: 5px;
	display: inline-block;
}

.img-box a{
	color:#fff;
}

.img-box:hover:after{
	opacity: 1;

}
.img-box:hover ul{
	opacity: 1;
}

.img-box ul a{
	-webkit-transition: all 0.3s ease-in-out 0s;
	transition: all 0.3s ease-in-out 0s;
}

.img-box a:hover li{
	border-color: #fff;
	color: orange;
}

